import React from 'react';
import { Row, Col } from 'antd';
import './style.scss';

export const StatisticBar = () => (
  <Row gutter={24} className="statistic-bar" type='flex' align="stretch">
    <Col xs={24} sm={24} md={20} lg={20} xl={20}>
      <Col xs={24} sm={24} md={8} lg={8} xl={8}>
        <Row>
          <Col span={18} className="title">Số tiền phải thu kỳ:</Col>
          <Col span={6} className="result">1,000,000</Col>
          <Col span={18} className="title">Số tiền đã thu kỳ:</Col>
          <Col span={6} className="result">1,000,000</Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={8} lg={8} xl={8}>
        <Row>
          <Col span={18} className="title">Số tiền thu thừa kỳ:</Col>
          <Col span={6} className="result">1,000,000</Col>
          <Col span={18} className="title">Số tiền còn phải thu kỳ:</Col>
          <Col span={6} className="result">1,000,000</Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={8} lg={8} xl={8}>
        <Row>
          <Col span={18} className="title">Số tiền thu thừa cuối kỳ:</Col>
          <Col span={6} className="result">1,000,000</Col>
          <Col span={18} className="title">Số tiền còn phải thu cuối cùng:</Col>
          <Col span={6} className="result">1,000,000</Col>
        </Row>
      </Col>
    </Col>
    <Col xs={24} sm={24} md={4} lg={4} xl={4}>
      <Row style={{ height: '100%' }} type='flex' align='bottom'>
        <Col span={12} className="title">Tổng số SV</Col>
        <Col span={12} className="result">20</Col>
      </Row>
    </Col>
  </Row>
);



